@import url(https://fonts.googleapis.com/css?family=Nunito|Quicksand);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  font-family: 'Nunito', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1, h2, h3, h4, h5, h6 {
  font-family:'Quicksand', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Universal */

:root {
  --blue: #209CEE;
  --green: #23D160;
  --purple: #b11fee;
  --darkpurple: #42264e;
  --umber: #725E54;
  --midgreen: #78E0DC;
  --red: #FF3860;

  --primary: #ee6c4d;
  --secondary: #293241;
  --secondary_shade: #43526d;
  --secondary_shade_2: #596d92;
}

* {
  margin: 0;
  padding: 0;
  /* background-color: whitesmoke; */
  box-sizing: border-box;
  -ms-scroll-chaining: none;
      overscroll-behavior: none;
}

a {
  text-decoration: none;
  color: black;
}

li {
  list-style-position: inside
}

.modal_background {
  position: fixed;
  /* display: flex;
  justify-content: center;
  align-items: center; */
  top: 0px;
  left: 0px;
  height: 100%;
  width: 100%;
  z-index: 1;
  background-color: rgba(0, 0, 0, 0.5)
}

/* Common */

/* Score */

.score {
  position: absolute;
  top: 30px;
  left: 30px;
  font-size: 20px;
}

/* Alphabet */

.fa_icon {
  color: gray;
  font-size: 15px;
}

/* Alphabet */

.alphabet_container_outer {
  margin-top: 40px;
  display: flex;
  justify-content: center;
  flex-direction: row;
}

.alphabet_container_inner {
  display: flex;
  flex-direction: column;
  width: 470px;
  justify-content: space-between;
  height: 700px;
}

.alphabet_row_container {
  display: flex;
  flex-direction: row;
  align-items: center;
  position: relative;
  cursor: pointer;
}

.alphabet_row_selected {
  margin: 0 50px 0 0;
}


.alphabet_row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
}

.alphabet_row_container:hover::before {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  /* border: 3px solid var(--green); */
  border-radius: 5px;
  padding: 0 20px;
  left: -20px;
}

.alphabet_character_container {
  pointer-events: all;
}

.alphabet_character {
  font-size: 30px;
}

.alphabet_translation {
  text-align: center;
  color: rgb(110, 110, 110);
}

/* Test Character */

.test_character {
  font-size: 50px;
  text-align: center;
  margin-bottom: 50px;
  position: relative;
  cursor: pointer;
}

.alphabet_answer_popup {
  background-color: rgb(41, 41, 41);
  border-radius: 10px;
  color: white;
  left: 50%;
  padding: 5px 10px;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  position: absolute;
  font-size: 13px;
  /* font-weight: bold; */
}

.alphabet_answer_popup::before {
  content: '';
  width: 0; 
  height: 0;
  display: block;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  top: -6px;
  position: absolute;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-bottom: 8px solid rgb(41, 41, 41);
}

.sidebar_wrapper {
  background-color: var(--secondary);
  /* padding: 30px 20px 30px 40px; */
  transition: all 0.2s;
  width: 300px;
  z-index: 1;
  color: white;
  box-shadow: 26px 0px 82px -33px rgba(0, 0, 0, 0.1);
  /* overflow: scroll; */
}

.sidebar_inner_wrapper {
  /* position: relative; */
  height: 100%;
  display: flex;
  flex-direction: column;
}

.all_module_container {
  padding-left: 40px;
  padding-bottom: 40px;
  padding-right: 10px;
