  width: 50px;
}

.module_container:hover .module_circle {
  background: white;
}

.module_container:hover .module_line {
  min-width: 50px;
}

.module_circle {
  height: 1em;
  min-width: 1em;
  border-radius: 50%;
  border: 1px solid white;
  transition: all 0.1s;
  background: var(--secondary);
  z-index: 1;
}

.module_line {
  height: 1px;
  min-width: 20px;
