  transition: all 0.1s;
}

.module_name {
  /* background-color: white; */
  color: white;

}

.module_dropdown {
  position: relative;
  margin: 0 0 0 2.5em;
  transition: all 1s;
  overflow: hidden;
}

.module_dropdown_line {
  position: absolute;
  width: 1px;
  height: calc(100% - 0.5em);
  left: 7px;
  background-color: white;
}

.sidebar_collapse {
  font-size: 30px;
  cursor: pointer;
  position: fixed;
  transition: left 0.2s, color 0.1s, -webkit-transform 0.2s;
  transition: left 0.2s, transform 0.2s, color 0.1s;
  transition: left 0.2s, transform 0.2s, color 0.1s, -webkit-transform 0.2s;
