/* Comparison Table */

.comparison_table {
  width: 80%;
  margin: 0 0 40px 0;
  line-height: 1.8;
  align-self: center;
  border-collapse: collapse;
  border-radius: 5px 5px 0 0;
  overflow: hidden;
}

.comparison_table th {
  text-align: left;
  padding: 20px 30px;
}

.comparison_table tr:nth-child(n+1) {
  border-bottom: 1px solid #c7c7c7;
}

.comparison_table tr:last-child {
  border-bottom: 0px;
}

.comparison_table td:last-child {
  /* background-color: rgb(223, 223, 223); */
}

.comparison_table tr:nth-child(even) {
  background-color: transparent;
}

.comparison_table td:first-child {
  /* background-color: rgb(223, 223, 223); */
}

.comparison_table td {
  text-align: center;
  width: 50%;
  padding: 20px 30px;
}

